import { useState, useEffect } from 'react';
import styles from './Colors.module.scss'

import { useTheme } from '../hooks/ThemeCtx'
import { motion } from "framer-motion";
import { pageVariants } from "../App";
import SectionTitle from '../comps/sectionTitle'
import SortSearch from '../comps/sortSearch'

const sortOptions = [
    "name",
    "red value",
    "green value",
    "blue value",
]

export default function Colors () {
    const { dark } = useTheme()
    const [ colors, setColors ] = useState([])
    const [ data, setData ] = useState([])
    const [search, setSearch] = useState("")
    const [sort, setSort] = useState("name")

  useEffect(() => {
    let ff = data.filter(color => {
      return color.name.includes(search.toLowerCase())
    })
    ff.sort((a,b) => {
      switch(true) {
      case (sort === "name"):
        switch (true){
          case (a.name < b.name):
            return -1;
          case (a.name > b.name):
            return 1;
          default:
            return 0;
        }
      case (sort === "red value"):
        switch (true){
        case (a.hex.slice(1,3) > b.hex.slice(1,3)):
            return -1;
        case (a.hex.slice(1,3) < b.hex.slice(1,3)):
            return 1;
          default:
            return 0;
        }
      case (sort === "green value"):
        switch (true){
        case (a.hex.slice(3,5) > b.hex.slice(3,5)):
            return -1;
        case (a.hex.slice(3,5) < b.hex.slice(3,5)):
            return 1;
          default:
            return 0;
        }
      default:
        switch (true){
        case (a.hex.slice(5,7) > b.hex.slice(5,7)):
            return -1;
        case (a.hex.slice(5.7) < b.hex.slice(5,7)):
            return 1;
          default:
            return 0;
        }
      }
    })
    setColors(ff)
  }, [data, data.length, search, sort])

  function getData () {
    fetch("/css-named-colors.json")
      .then(response => {
        return response.json()
      })
      .then(objs => {
        setData(objs)
      })
  }

  useEffect (() => {
     getData()
  }, [])

  return (
  <motion.div className={styles.main}
    variants={pageVariants}
    initial="hidden"
    animate="show"
    exit="exit"
  >
      <SectionTitle>CSS Named Colors</SectionTitle>

      <SortSearch sortOptions={sortOptions} search={search} setSearch={setSearch} sort={sort} setSort={setSort}/>

      <motion.div className={styles.table} layout transition={{ duration: 0.3 }}>
        {
          colors.map(color => {
            return (
            <div key = {color.name} className={styles.row}
                style={{
                  color: dark ? "#fff" : "#000",
                  backgroundColor: dark ? "#000" : "#fff",
                  border: `1px solid ${color.name}`
                }}
            >
              <div>
                <div className={styles.name}>
                  {color.name}
                </div>
                <div className={styles.hex}>
                  {color.hex}
                </div>
                <div className={styles.rgb}>
                  {color.rgb}
                </div>
              </div>
              <div className={styles.box}
                style={{
                  color: `${color.name}`,
                  backgroundColor: `${color.name}`,
                }}
              >
              </div>
            </div>
            )
          })
        }
      </motion.div>
    </motion.div>
  )
}
